<template>
  <div class="paymentPaySuccess">
    <!--头部栏-->
    <van-nav-bar class="task-nav-bar" title="支付成功" :border="false" left-arrow @click-left="goBack" />

    <div class="headerColor">
      <div class="headImg">
        <img src="@/assets/images/common/img_payment_successful.png" />
      </div>
      <div class="content">
        <div class="contentSecond">
          <span class="moneyIcon">￥</span>
          <span class="moneyNum">{{orderDetail.realPayTotal | keepTwoNum}}</span>
        </div>
        <div class="contentThird">
          <span v-if="orderDetail.pointIncome>0">
            恭喜获得
            <span>{{orderDetail.pointIncome}}</span>积分！
          </span>
          <span
            style="color: #CE9D6A;text-decoration:underline"
            v-if="awardShow==0"
            @click="goToActivity()"
          >点此参与大转盘活动</span>
        </div>
      </div>
      <div class="finishDiv">
        <!--确认按钮-->
        <div class="finishButton" @click="finishCommit()">回到首页</div>
        <div class="finishButton seeOrder" @click="seeOrder()">查看订单</div>
      </div>
    </div>
    <!-- </ion-view> -->
  </div>
</template>

<script>
import {
  selectConsumedService,
  orderDetailService
} from "../../../services/productOrderService.js";

export default {
  name: "paymentPaySuccess",
  components: { },
  data() {
    return {
      orderDetail: {}, //订单信息
      promotion: {}, //活动信息
      awardShow: 1 //是否显示转盘
    };
  },
  mounted() {
  },
  activated() {
    this.orderDetail = this.$route.params.orderData?this.$route.params.orderData:this.orderDetail;
    console.log(JSON.stringify(this.orderDetail));
    if (this.orderDetail.billType == "预缴费") {
      this.orderDetail.realPayTotal = this.orderDetail.unionPayMoney;
      this.orderDetail.pointIncome = this.orderDetail.getPoints;
    } else {
      this.orderDetail.realPayTotal = this.orderDetail.realPayAmount;
      this.orderDetail.pointIncome = this.orderDetail.obtainable_piont;
    } 
    this.getAreaActivity();
  },
  methods: {
    //跳转订单详情
    seeOrder: function() {
      if (
        this.orderDetail.billType == "预缴费" ||
        this.orderDetail.billType == "账单缴费"
      ) {
        this.$router.push({ name: "propertyOrderList", params: { flag: 10 } });
      }
    },

    //回到首页
    finishCommit: function() {
      if (
        this.orderDetail.billType == "预缴费" ||
        this.orderDetail.billType == "账单缴费"
      ) {
        this.$router.push({ name: "homePage" });
      }
    },

    //跳转活动转盘页面
    goToActivity: function() {
      this.$router.push({
        path: "/pointActivity",
        query: {
          promotionId: this.promotion.promotionId,
          title: this.promotion.promotionName,
          pointConsumed: this.promotion.pointConsumed
        }
      });
    },

    //获取小区活动
    getAreaActivity: function() {
      selectConsumedService(this.$userInfo.AREA_CODE)
        .then(result => {
          this.awardShow = result.delFlag; // 0 显示
          this.promotion = result; //转盘活动id
        })
        .catch(error => {});
    },
    // 支付完成后获取订单详情查询订单状态
    getOrderDetail: function() {
      var data = {
        billNo: this.orderDetail.billNo,
        ownerCode: this.$userInfo.OWNER_CODE
      };
      orderDetailService(data)
        .then(result => {
          if ("0" == result.resultCode) {
            //true表示返回成功
            console.log("支付页查看订单状态" + JSON.stringify(result));
            this.orderDetail = result.data;
          }
        })
        .catch(error => {});
    },

    goBack: function() {
      if (
        this.orderDetail.billType == "预缴费" ||
        this.orderDetail.billType == "账单缴费"
      ) {
        this.$router.push({ name: "homePage" });
      }
    }
  }
};
</script>

<style>
/*头部*/
.paymentPaySuccess .headerColor {
  background: #ffffff;
  /*border-bottom: 1px solid #ECECEC;*/
  /* -moz-box-shadow: 0px 4px 4px #F0F0F4;
    -webkit-box-shadow: 0px 4px 4px #F0F0F4;
    box-shadow: 0px 4px 4px #F0F0F4; */
}

.paymentPaySuccess .bar .title {
  font-size: 18px;
}

.paymentPaySuccess .formStyle {
  display: block;
  margin-left: 0.75rem;
}

.paymentPaySuccess .headImg {
  padding-top: 101px;
  margin-bottom: 25px;
  text-align: center;
}

.headImg img {
  width: 90px;
  height: 112px;
}

.paymentPaySuccess .content {
  text-align: center;
}

.paymentPaySuccess .contentFirst {
  font-family: PingFangSC-Medium;
  font-size: 18px;
  color: #1a1a1a;
  letter-spacing: 0.07px;
  /*width: 80%;*/
  /*margin-left: 10%;*/
  text-align: center;
  height: 40px;
  line-height: 40px;
}

.paymentPaySuccess .contentSecond {
  text-align: center;
  height: 40px;
}

.contentSecond .moneyIcon {
  font-size: 18px;
  color: #3a3a3a;
  letter-spacing: 0.07px;
  line-height: 40px;
}

.contentSecond .moneyNum {
  font-size: 28px;
  color: #000000;
  letter-spacing: 0.07px;
  line-height: 40px;
}

.paymentPaySuccess .contentThird {
  font-family: PingFangSC-Regular;
  font-size: 14px;
  color: #3a3a3a;
  letter-spacing: 0.06px;
  text-align: center;
  height: 40px;
  line-height: 40px;
}

.paymentPaySuccess .finishDiv {
  /* height: 40px; */
  display: flex;
  justify-content: space-between;
}

.paymentPaySuccess .finishButton {
  text-align: center;
  font-size: 16px;
  color: #17B7BD;
  width: 32%;
  margin-left: 14%;
  margin-top: 40px;
  height: 44px;
  line-height: 42px;
  background: #ffffff;
  border: 1px solid #17B7BD;
  border-radius: 22px;
}

.paymentPaySuccess .seeOrder {
  margin-right: 14%;
  margin-left: 0;
  background: #17B7BD;
  box-shadow: 0 0 15px 0 rgba(165, 197, 165, 0.39);
  color: #ffffff !important;
  /* border: none !important; */
}
</style>